require("@babel/polyfill");

import Prism from 'prismjs';
var formatter = require('html-formatter');
var Normalizer = require('prismjs/plugins/normalize-whitespace/prism-normalize-whitespace');
Prism.hooks.add('before-sanity-check', (env) => {
  env.code = env.element.innerText;
});

import rwxPreload from 'roseworx/js/modules/rwxPreload';

import rwx from 'roseworx';

require('./site-styles.scss');

window.addEventListener('load', ()=>{

	const ta = document.createElement('textarea');
	ta.style.position = "fixed";
	ta.style.width = "0px";
	ta.style.height = "0px";
	document.body.appendChild(ta);

	[...document.querySelectorAll('.code-block')].map((cb)=>{
		let toCopy;
		if(cb.hasAttribute('codeblock'))
		{
			let id = cb.getAttribute('data-code-block-id');
			let el = document.querySelector(`[cb='${id}']`);
			if(!el){console.log(`No Code Block element found with id: ${id}`);return;}
			el.removeAttribute('cb');
			toCopy = formatter.render(el.outerHTML.replace(/=""/g, ""));
			cb.querySelector('code').innerText = toCopy;
		}
		else
		{
			let code = cb.querySelector('code');
			code.innerText = code.innerText.replace(/	/g, "").replace(/  /g, "");
			toCopy = code.innerText;
		}

		cb.addEventListener("click", ()=>{
			ta.value = toCopy;
			let copyText = ta;
		  copyText.select();
		  copyText.setSelectionRange(0, 99999);
		  document.execCommand("copy");
		  cb.classList.add('copied');
		  setTimeout(()=>{cb.classList.remove("copied")}, 5000);
		});

		if(cb.hasAttribute('codeblock')) {
			setTimeout(() => {
				cb.classList.add('notactive');
			}, 50);
		}

		return;
	});

	Prism.highlightAll();

	const navIcon = document.getElementById('navigation-icon');
	const nav = document.getElementById('navigation');
	navIcon.addEventListener('click', ()=>{
		nav.classList.toggle('open');
		navIcon.classList.toggle('active');
	});

	const codeBlockIcon = document.getElementById('code-block-icon');
	const codeBlocks = [...document.querySelectorAll('.code-block')];
	codeBlockIcon && codeBlockIcon.addEventListener('click', ()=>{
		codeBlocks.map((cb)=>cb.classList.toggle('notactive'));
		codeBlockIcon.classList.toggle('active');
	});
});